import React from "react";
import { Helmet } from "react-helmet";

const EcommerceMetaRoute = () => (
  <div>
    <Helmet>
      <meta charSet="utf-8" />
      <meta
        name="ecommerce"
        content="Alero is a Product Design Agency
"
      />

      <title>Alero | Ecommerce</title>
    </Helmet>
  </div>
);

export default EcommerceMetaRoute;
