import React from "react";
import { Helmet } from "react-helmet";

const BrandingMetaRoute = () => (
  <div>
    <Helmet>
      <meta charSet="utf-8" />
      <meta
        name="branding"
        content="Alero is a Product Design Agency
"
      />
     

      <title>Alero | Branding</title>
    </Helmet>
  </div>
);

export default BrandingMetaRoute;
