import styled from "styled-components";

export const Wrapper = styled.div`
  width: 100%;
  oveflow: hidden;
  margin-bottom: 50px;
  
`;
export const MainWrapper = styled.div`
  @media all and (min-width: 1366px) {
    background: linear-gradient(
      180.09deg,
      #fef0d4 0.08%,
      #fef5e3 12.12%,
      #ffffff 27.07%
    );

    display: flex;
    grid-template-columns: 0.7fr 1fr;
    position: relative;
  }
`;
export const Container = styled.div`
  width: 100%;
  height: 100%;

  @media all and (min-width: 768px) {
    width: 690px;
    margin: auto;
  }
  @media all and (min-width: 1366px) {
    width:100%;
  }
`;

export const HeadWrap = styled.div`
  width: 90%;
  margin: auto;
  margin-bottom: 20px;
  @media all and (min-width: 446px) {
    width: 401.4px;
  }
  @media all and (min-width: 554px) {
    width: 450.4px;
  }
  @media all and (min-width: 720px) {
    width: 648px;
  }
  @media all and (min-width: 1366px) {
    width: 100%;
    background: transparent;
    padding-top: 180px;

  }
`;

export const HeadingText = styled.h5`
  font-size: 21.7077px;
  line-height: 25px;
  letter-spacing: 0.01em;
`;

export const HeadingSubText = styled.p`
  font-size: 12.7713px;
  line-height: 35px;
`;

export const ServiceWrapper = styled.div`
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-gap: 0.5rem;
  width: 90%;
  margin: auto;
  justify-content: center;
  align-content: center;

  @media all and (min-width: 425px) {
    width: 382.5px;
  }
  @media all and (min-width: 720px) {
    width: 648px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr;
  }
  @media all and (min-width: 1366px) {
    width: 648px;
    justify-content: left;
    align-content: left;
    margin-left: -20px;
    z-index: 20;
  }
`;

export const ContentDetailWrapper = styled.div`
  z-index: 40;
  background: red;
  width: 188px;
  height: 192px;
  margin: auto;
`;
export const Content = styled.div`
  border-radius: 13.1259px;
  z-index: 2;
  width: 168px;
  height: 192px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  background: ${({ checkId, active }) =>
    checkId === active.id && active.active
      ? "white"
      : "rgba(242, 141, 119, 0.3)"};
  box-shadow: ${({ checkId, active }) =>
    checkId === active.id && active.active
      ? "0px 0px 22.304px #e0ecd1"
      : "none"}; 
`;

export const EmptySelection = styled.div`
  position: absolute;
  width: 26.24px;
  height: 24.06px;
  background: #ffffff;
  left: 126.85px;
  top: 15.31px;
`;

export const FullSelection = styled.div`
  position: absolute;
  width: 26.24px;
  height: 24.06px;
  left: 126.37px;
  top: 15.09px;
  background: #e0ecd1;
  display: flex;
  align-items: center;
  justify-content: center;
`;
export const SelectionCheck = styled.div`
  width: 15.61px;
  height: 15.61px;
  left: 131.95px;
  top: 19.55px;

  background: #3a7a44;
  border-radius: 10.0368px;
`;
export const ContentText = styled.h5`
  font-size: 16.4027px;
  line-height: 22px;
  letter-spacing: -0.01em;
  text-align: center;
  color: #151515;
`;

export const Image = styled.img`
  width: 73px;
  height: 50px;
  margin-bottom: 38px;
  z-index: -20;
`;

export const FormWrapper = styled.div`
  width: 100%;
  text-align: center;
  margin-top: 30px;
  margin-bottom: 30px;
  @media all and (min-width: 1000px) {
    grid-area: col1;
  }
  @media all and (min-width: 768px) {
    width: 690px;
  }
`;

export const Form = styled.form`
  display: flex;
  flex-direction: column;
  width: 80%;
  margin: auto;
  @media all and (min-width: 768px){
    width: 100%;
  }
`;
export const FormContentWrap = styled.div`
  @media all and (min-width: 768px) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
    width: 100%;
    justify-content: space-between;
  }
`;
export const FormP = styled.p`
  font-weight: normal;
  font-size: 14.6466px;
  line-height: 20px;
  text-align: justify;
  margin-bottom: 21px;
  margin-top: 20px;
  a {
    font-weight: bold;
    font-size: 14.6466px;
    line-height: 20px;
    text-align: justify;
    text-decoration-line: underline;
    margin-left: 10px;
    margin-right: 10px;
    color: #3a7a44;
  }

  @media all and (min-width: 1000px) {
    font-size: 16px;
  }
  @media all and (min-width: 768px) {
    text-align: left;
    font-size: 16px;
    width: 390px;
    margin-bottom: 0px;
    line-height: 24px;
  }
`;
export const FormError = styled.p`
  color: red;
`;


export const FormContent = styled.div`
  width: 100%;
  margin-top: 20px;
  text-align: left;


`;
export const FormLabel = styled.label`
  font-family: Noto Sans;
  font-weight: 500;
  font-size: 17.0927px;
  line-height: 25px;
  letter-spacing: 0.01em;
  margin-bottom: 16px;
`;

export const FormInput = styled.input`
  margin-top: 11px;
  background: #fcfcfc;
  border-radius: 12.7713px;
  height: 51px;
  border: 2.17077px solid #a5c77d;
  outline: none;
  text-indent: 20px;
  font-size: 17px;
  width: 100%;
  ::placeholder {
    color: #3a7a44;
    font-size: 17px;
  }
`;

export const FormTextArea = styled.textarea`
  margin-top: 11px;
  height: 148px;
  border: 2.26667px solid #a5c77d;
  border-radius: 12.7713px;
  outline: none;
  width: 100%;
  text-align: justify;
  text-indent: 20px;
  padding-top: 1rem;

  ::placeholder {
    color: #3a7a44;
    font-size: 17px;
  }
`;
export const FormButton = styled.button`
  width: 256.65px;
  margin: auto;
  height: 57.38px;
  color: #ffffff;
  background: radial-gradient(135% 135% at 5.8% 0%, #a5c77d 0%, #3a7a44 100%);
  font-weight: 600;
  font-size: 18.7791px;
  line-height: 26px;
  margin-top: 30px;
  outline: none;
  border: 0.05px solid transparent;
  border-radius: 3rem;
  box-shadow: 21.9089px 21.9089px 57.3805px rgba(0, 0, 0, 0.129);


  
`;

export const ContactDetails = styled.div`
  width: 90%;
  margin: auto;
  @media all and (min-width: 768px) {
    width: 100%;
  }
  @media all and (min-width: 1366px) {
    position: absolute;

    top: 853px;
    text-align: left;
    width: 400px;
    left: ${({ widthchange }) => `${widthchange * 0.1}px`};
    z-index: 20;
  }
  @media all and (min-width: 1891px) {
    left: ${({ widthchange }) => `${widthchange * 0.14}px`};
  }
  @media all and (min-width: 2661px) {
    position: relative;

    top: 0px;
    text-align: left;
    width: 100%;
    left: 0;
  }
`;

export const CallDetails = styled.div`
  margin-bottom: 16px;
  h5 {
    font-weight: normal;
    font-size: 17.8283px;
    line-height: 21px;
    letter-spacing: -0.04em;

    color: #151515;
  }

  p {
    font-weight: bold;
    font-size: 25.6566px;
    line-height: 42px;
    color: #264d2d;
  }
`;

export const EmailDetails = styled.div`
  margin-bottom: 26px;
  h5 {
    font-weight: normal;
    font-size: 17.8283px;
    line-height: 21px;
    letter-spacing: -0.04em;

    color: #151515;
  }

  p {
    font-weight: bold;
    font-size: 25.6566px;
    line-height: 42px;
    color: #264d2d;
  }
`;
