import React from "react";
import { Helmet } from "react-helmet";

const VisualBrandMetaRoute = () => (
  <div>
    <Helmet>
      <meta charSet="utf-8" />
      <meta
        name="visualbrand"
        content="Alero is a Product Design Agency
"
      />

      <title>Alero | Visual Brand</title>
    </Helmet>
  </div>
);

export default VisualBrandMetaRoute;
