import React from "react";
import { Helmet } from "react-helmet";

const OurWorkMetaRoute = () => (
  <div>
    <Helmet>
      <meta charSet="utf-8" />
      <meta
        name="ourwork"
        content="Alero is a Product Design Agency
"
      />

      <title>Alero | Our Work</title>
    </Helmet>
  </div>
);

export default OurWorkMetaRoute;
