import React from "react";
import { Helmet } from "react-helmet";

const ContactMetaRoute = () => (
  <div>
    <Helmet>
      <meta charSet="utf-8" />
      <meta
        name="contact"
        content="Alero is a Product Design Agency
"
      />

      <title>Alero | Contact Us</title>
    </Helmet>
  </div>
);

export default ContactMetaRoute;
